.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.kanjiContainer {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.kanji {
    font-size: 40px; 
    border: 1px solid black;
    padding: 1em;
    background-color: aliceblue;
    margin-top: 0;
}

.kanjiLeft {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-right: 1em;
}

.kanjiLeft > * {
    margin: 0 0 1em 0;
}


.wordNumber {
    border: 1px solid black;
    background-color: aliceblue;
    padding: 0.25em;
}

.help {
    padding: 0em 1em;
    margin: 0em;
    min-width: 200px;
}


.helpButton {
    padding: 0.5em 0.5em;
    margin: 0m 1em;
    background-color: rgb(197, 190, 190);
    font-weight: bold;
}

.shuffleButton {
    padding: 0.5em 0.5em;
    margin: 0m 1em;
    background-color: aliceblue;
    font-weight: bold;
}


input[type="text"] {
    padding: 1em 1em;
    margin: 1em 1em;
    box-sizing: border-box;
    background-color: aliceblue;
    width: 100%;
}

input[type="submit"] {
    padding: 1em 1em;
    margin: 1em 1em;
    background-color: rgb(197, 190, 190);
    font-weight: bold;
}

.error {
    animation-duration: 0.5s;
    animation-name: mistake;
}

@keyframes mistake {
    0% {background-color: red;}
    25% {transform: rotate(5deg);}
    50% {transform: rotate(-5deg);}
    75% {transform: rotate(5deg);}
    100% {transform: rotate(-5deg);}
}

select {
    padding: 0.5em;
    margin-left: 1em;
    display: block;
}